
.cards{
  display: flex;
  flex-wrap:wrap;
  height:auto;
  width:auto;
}
.testimonial{
  height:auto;
  width:100%;
  background-position: fixed;
  background-repeat: no-repeat;
  text-align:center;

}
.our-mission{
  font-size:24px;
  color:#FF5200;
  padding:50px;
  
  
}
.our-test{
  font-size:38px;
  color:black;
  padding-bottom:70px;
}
.card{
height:350px;
width:200px;
padding:50px;
margin:75px;
box-shadow: 10px 10px 20px #FF5200 ;

}
.card:hover{
  transform: scale(1.09);
  transition:ease-in-out 1s;
}
.comma{
height:70px;
width:70px;
float:left;


}
.description{
font-size:12px;
text-align:left;
margin-top:30px;
}
.pic-name{
  display: flex;
  margin-top:40px;

  
}
.image{
  height:100px;
  width:100px;
  border-radius: 50%;
}
.name{
  font-size:16px;
  margin-top:50px;
  margin-left:10px;
}